<template>
    <div 
        @mousedown="mousedown"
        @mousemove="mousemove"
        @mouseup="mouseup"
        class="alt-grid-container" 
        :style="containerStyle">
        <div 
            v-for="(item, index) in layout"
            :key="index"
            :style="getCardStyle(item)"
            class="alt-grid-item">
            itemitem
            <span class="alt-grid-item-resize-handler"></span>
        </div>
    </div>
</template>

<script>
    import { deepCopy, hasClass } from './utils/util';

    export default {
        name: 'app',
        props: {
            isDraggable: { // 
                type: Boolean,
                default: true
            },
            isResizable: { // 
                type: Boolean,
                default: true
            },
            rowHeight: { // 
                type: Number,
                default: 150
            },
            maxRows: { // 
                type: Number,
                default: Infinity
            },
            margin: {
                type: Array,
                default: () => [10, 10]
            }, // 
            verticalCompact: { // 
                type: Boolean,
                default: true
            },
            useCssTransforms: { // css transforms
                type: Boolean,
                default: true
            },
            colNum: { // 
                type: Number,
                default: 12
            },
            backgroundColor: { // 
                type: String,
                default: 'rgba(200,200,200,1)'
            },
            gridItemClass: { // class
                type: String,
                default: ''
            },
            closeHandlerClass: { // class
                type: String,
                default: ''
            },
            resizeHandlerClass: { // class
                type: String,
                default: 'alt-grid-item-resize-handler'
            },
            placeholderClass: { //  placeholder class
                type: String,
                default: ''
            },
            isShowOriginCloseBtn: {
                type: Boolean,
                default: true
            }
        },
        data () {
            return {
                layout: [], // 
                defVal: {
                    minH: 1, // 
                    minW: 1, // 
                    maxH: Infinity, // 
                    maxW: Infinity, // 
                    isDraggable: true, // 
                    isResizable: true, // 
                    isShowOriginCloseBtn: true // 
                },
                containerHeight: 0, // 
                cols: [],
                cacheComputed: {}
            }
        },
        mounted: function () {
            this.initCols();
        },
        watch: {
            rowHeight(val){
                this.cell.height = val;
            }
        },
        computed: {
            containerStyle(){
                return {
                    height: this.containerHeight + 'px'
                }
            }
        },
        methods: {
            // 
            initCols(){
                let colNum = this.colNum;
                let cols = this.cols;
                let containerWidth = this.$el.offsetWidth;
                let remainder = containerWidth % colNum; // 
                let quotient = Math.floor(containerWidth / colNum); // 
                for(let i = 0; i < colNum; i++){
                    if(remainder){
                        cols[i] = quotient + 1;
                        remainder--;
                    }else{
                        cols[i] = quotient;
                    }
                }
            },
            // layout
            setLayout(layout){
                this.layout = deepCopy(layout);
                // this.layout = layout;
            },
            // 
            setContainerHeight(y, h){
                let containerHeight = this.containerHeight;
                let height = y + h;
                if(height > containerHeight){
                    this.containerHeight = height;
                }
            },
            // 
            getCardStyle(item){
                let x = this.computeColsWidth(0, item.x);
                let w = this.getCardWidth(item.x, item.x + item.w);
                let y = item.y * (this.rowHeight + this.margin[1]);
                let h = item.h * (this.rowHeight + this.margin[1]) - this.margin[1];
                this.setContainerHeight(y, h);
                return {
                    transform: `translate(${x}px,${y}px)`,
                    width: w + 'px',
                    height: h + 'px'
                }
            },
            // 
            getCardWidth(start, end){
                let width = this.computeColsWidth(start, end);
                if(end !== (this.cols.length)){
                    width -= this.margin[0];
                }
                return width;
            },
            // 
            computeColsWidth(start, end){
                let key = start + ';' + end;
                if(this.cacheComputed[key]) return this.cacheComputed[key];
                let cols = this.cols;
                let width = 0;
                for(let i = start; i < end; i++){
                    width += cols[i];
                }
                this.cacheComputed[key] = width;
                return width;
            },
            mousedown(evt){
                console.log('down', evt);
                if(hasClass(evt.target, this.resizeHandlerClass)){
                    this.operater = 2
                } else {
                    this.operater = 1;
                }
            },
            mousemove(evt){
                if(this.operater){
                    console.log('move', evt);
                }
            },
            mouseup(evt){
                console.log('up', evt);
                this.operater = 0
            }
        }
    }
</script>

<style>
.alt-grid-container{
    position: relative;
    border: 1px solid red;
    box-sizing: border-box;
}
.alt-grid-container .alt-grid-item{
    position: absolute;
    background: gray;
}
.alt-grid-container .alt-grid-item:hover .alt-grid-item-resize-handler{
    display: block;
}
.alt-grid-container .alt-grid-item-resize-handler{
    display: none;
    position: absolute;
    width: 0;
    height: 0;
    right: 1px;
    bottom: 1px;
    border-top: 5px solid transparent;
    border-left: 5px solid transparent;
    border-right: 5px solid #000;
    border-bottom: 5px solid #000;
    cursor: se-resize;
}
</style>
